.message {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}


.avatar {
    height: 50px;
    width: 50px;

    border-radius: 50%;
}

.content {
    padding: 10px;

    border-radius: 5px 5px 5px 0;
    background-color: rgba(255, 255, 255, 0.3);
}

.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(255, 255, 255, 0.3) 0);

}

.name {
  font-size: 20px;
    color: firebrick;
    font-weight: bold;
}

.text {
  min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;

}
.time{
    display: flex;
    justify-content: end;
    color: firebrick;
}